
body{
      font-family: 'Roboto';
      background-color: #f9ebe0;
      /*background-color: rgba(255, 255, 255, 0.3);*/
}
.bounce-in-top {
	animation: bounce-in-top 1.1s both;
}
.fadeIn {
animation: fadeIn 1.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
.fade-in-left {
	animation: fade-in-left 0.8s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}
@keyframes fade-in-left {
0% {
    transform: translateX(-50px);
    opacity: 0;
}
100% {
    transform: translateX(0);
    opacity: 1;
}
}
.fade-in-top {
	animation: fade-in-top 0.8s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}
@keyframes fade-in-top {
0% {
    transform: translateY(-50px);
    opacity: 0;
}
100% {
    transform: translateY(0);
    opacity: 1;
}
}
.fade-in-right {
	animation: fade-in-right 0.8s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}
@keyframes fade-in-right {
0% {
    transform: translateX(50px);
    opacity: 0;
}
100% {
    transform: translateX(0);
    opacity: 1;
}
}
@keyframes bounce-in-top {
0% {
    transform: translateY(-500px);
    animation-timing-function: ease-in;
    opacity: 0;
}
38% {
    transform: translateY(0);
    animation-timing-function: ease-out;
    opacity: 1;
}
55% {
    transform: translateY(-65px);
    animation-timing-function: ease-in;
}
72% {
    transform: translateY(0);
    animation-timing-function: ease-out;
}
81% {
    transform: translateY(-28px);
    -webkit-animation-timing-function: ease-in;
}
90% {
    transform: translateY(0);
    animation-timing-function: ease-out;
}
95% {
    transform: translateY(-8px);
    animation-timing-function: ease-in;
}
100% {
    transform: translateY(0);
    animation-timing-function: ease-out;
}
}
@keyframes fadeIn {
0% {
    transform: translateZ(-1400px);
    opacity: 0;
}
100% {
    transform: translateZ(0);
    opacity: 1;
}
}

